import { writable } from "svelte/store";
import BinaryValue from "../util/BinaryValue";
import { isSet, set, str } from "../util/localStorage";
export var main_data_bus = writable(new BinaryValue(16, 0));
export var ir_cu_data_bus = writable(new BinaryValue(16, 0));
export var alu_acc_data_bus = writable(new BinaryValue(16, 0));
export var alu_sw_data_bus = writable(new BinaryValue(16, 0));
export var mux_alu_data_bus = writable(new BinaryValue(16, 0));
export var cu_mux_ctrl_bus = writable(new BinaryValue(16, 0));
export var cu_ram_ctrl_bus = writable(new BinaryValue(16, 0));
export var cu_alu_ctrl_bus = writable(new BinaryValue(16, 0));
export var main_address_bus = writable(new BinaryValue(16, 0));
export var inc_pc_address_bus = writable(new BinaryValue(16, 0));
// COLORS
// data bus
export var externalDataBusColor = writable(isSet("externalDataBusColor") ? str("externalDataBusColor") : "#0000ff");
export var internalDataBusColor = writable(isSet("internalDataBusColor") ? str("internalDataBusColor") : "#00ffff");
export var externalDataBusAnimationColor = writable(isSet("externalDataBusAnimationColor") ? str("externalDataBusAnimationColor") : "#00ff00");
export var internalDataBusAnimationColor = writable(isSet("internalDataBusAnimationColor") ? str("internalDataBusAnimationColor") : "#006400");
// address bus
export var externalAddressBusColor = writable(isSet("externalAddressBusColor") ? str("externalAddressBusColor") : "#ff8c00");
export var internalAddressBusColor = writable(isSet("internalAddressBusColor") ? str("internalAddressBusColor") : "#ffff00");
export var externalAddressBusAnimationColor = writable(isSet("externalAddressBusAnimationColor") ? str("externalAddressBusAnimationColor") : "#00ff00");
export var internalAddressBusAnimationColor = writable(isSet("internalAddressBusAnimationColor") ? str("internalAddressBusAnimationColor") : "#006400");
// control bus
export var externalControlBusColor = writable(isSet("externalControlBusColor") ? str("externalControlBusColor") : "#ff0000");
export var internalControlBusColor = writable(isSet("internalControlBusColor") ? str("internalControlBusColor") : "#ff7a90");
export var externalControlBusAnimationColor = writable(isSet("externalControlBusAnimationColor") ? str("externalControlBusAnimationColor") : "#00ff00");
export var internalControlBusAnimationColor = writable(isSet("internalControlBusAnimationColor") ? str("internalControlBusAnimationColor") : "#00ff00");
// subscribers so that each value is stored locally
externalDataBusColor.subscribe(function (newValue) { return set("externalDataBusColor", newValue); });
internalDataBusColor.subscribe(function (newValue) { return set("internalDataBusColor", newValue); });
externalDataBusAnimationColor.subscribe(function (newValue) { return set("externalDataBusAnimationColor", newValue); });
internalDataBusAnimationColor.subscribe(function (newValue) { return set("internalDataBusAnimationColor", newValue); });
externalAddressBusColor.subscribe(function (newValue) { return set("externalAddressBusColor", newValue); });
internalAddressBusColor.subscribe(function (newValue) { return set("internalAddressBusColor", newValue); });
externalAddressBusAnimationColor.subscribe(function (newValue) { return set("externalAddressBusAnimationColor", newValue); });
internalAddressBusAnimationColor.subscribe(function (newValue) { return set("internalAddressBusAnimationColor", newValue); });
externalControlBusColor.subscribe(function (newValue) { return set("externalControlBusColor", newValue); });
internalControlBusColor.subscribe(function (newValue) { return set("internalControlBusColor", newValue); });
externalControlBusAnimationColor.subscribe(function (newValue) { return set("externalControlBusAnimationColor", newValue); });
internalControlBusAnimationColor.subscribe(function (newValue) { return set("internalControlBusAnimationColor", newValue); });
export default {
    main_data_bus: main_data_bus,
    ir_cu_data_bus: ir_cu_data_bus,
    alu_acc_data_bus: alu_acc_data_bus,
    alu_sw_data_bus: alu_sw_data_bus,
    mux_alu_data_bus: mux_alu_data_bus,
    cu_mux_ctrl_bus: cu_mux_ctrl_bus,
    cu_ram_ctrl_bus: cu_ram_ctrl_bus,
    cu_alu_ctrl_bus: cu_alu_ctrl_bus,
    main_address_bus: main_address_bus,
    inc_pc_address_bus: inc_pc_address_bus,
    externalDataBusColor: externalDataBusColor,
    internalDataBusColor: internalDataBusColor,
    externalAddressBusColor: externalAddressBusColor,
    internalAddressBusColor: internalAddressBusColor,
    externalControlBusColor: externalControlBusColor,
    internalControlBusColor: internalControlBusColor,
    externalDataBusAnimationColor: externalDataBusAnimationColor,
    internalDataBusAnimationColor: internalDataBusAnimationColor,
    externalAddressBusAnimationColor: externalAddressBusAnimationColor,
    internalAddressBusAnimationColor: internalAddressBusAnimationColor,
    externalControlBusAnimationColor: externalControlBusAnimationColor,
    internalControlBusAnimationColor: internalControlBusAnimationColor
};
